// app/javascript/packs/front-end-react/apollo.js
const ApolloClient = require('apollo-client').default
import gql from "graphql-tag";



export const client = new ApolloClient({
  uri: "/graphql",
})


// let's define graphql queries here, similar to what we send using rails Graphiql Engine

export const CREATE_TRANSACTION = gql `
  mutation CreateTransaction($amount: Float!, $account_id: Integer!){
    createTransaction(
      input:{
        amount: $amount
        account_id: $account_id
    }){
      transaction {
        id
        account_id
        amount
      }
    }
  }
`

// get a account balance
export const GET_ACCOUNT = gql`
  query account($accountId: ID!){
    account(id: $accountId){
      id
      balance
    }
  }
`